<template>
  <md-layout>
    <h1 class="md-headline">
      {{sections.services.heading}}
      <br/>
      <span class="md-caption" v-html="sections.services.subheading"></span>
    </h1>
    <div class="service-cards">
      <div class="service" :key="service.title" v-for="service in services">
        <div class="md-title">{{service.title}}</div>
        <div class="md-caption">{{service.subtitle}}</div>
        <p class="md-body-1">
          {{service.summary}}
        </p>
      </div>
    </div>
  </md-layout>
</template>
<script>
  import { mapGetters } from 'vuex'

  export default {
    computed: {
      ...mapGetters(['services', 'sections'])
    }
  }
</script>
<style>
  .service {
    width: 48%;
    display: inline-block;
    margin-bottom: 3%;
    padding: 2%;
    box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
  }
  
  .service:nth-child(even) {
    margin-left: 4%;
  }
  
  .md-card .md-title {
    font-size: 18px;
  }
</style>



