<template>
  <md-layout>
    <div class="full-width">
      <h1 class="md-headline">
        {{sections.contact.heading}}
        <br/>
        <span class="md-caption" v-html="sections.contact.subheading"></span>
      </h1>
    </div>
    <md-layout>
      <md-layout>
        <md-list class="md-double-line full-width">
          <md-list-item>
            <md-icon class="md-primary">person_outline</md-icon>
            <div class="md-list-text-container">
              <span v-html="myInfo.name"></span>
              <span>Name</span>
            </div>
          </md-list-item>
          <md-divider></md-divider>
          <md-list-item>
            <md-icon class="md-primary">location_on</md-icon>
            <div class="md-list-text-container">
              <span>Rawalpindi, Pakistan</span>
              <span>Location</span>
            </div>
          </md-list-item>
        </md-list>
      </md-layout>
      <md-layout>
        <md-list class="md-double-line full-width">
          <md-list-item>
            <md-icon class="md-primary">mail_outline</md-icon>
            <div class="md-list-text-container">
              <span v-html="myInfo.email"></span>
              <span>Email</span>
            </div>
          </md-list-item>
          <md-divider></md-divider>
          <md-list-item>
            <md-icon class="md-primary">chat_bubble_outline</md-icon>
            <div class="md-list-text-container">
              <span v-html="myInfo.skype"></span>
              <span>Skype</span>
            </div>
          </md-list-item>
        </md-list>
      </md-layout>
    </md-layout>
    <md-whiteframe md-elevation="1" class="full-width contact-form">
      <!--<md-progress class="md-accent progress-custom" md-indeterminate :class="{ 'show': loading}"></md-progress>-->
      <form novalidate @submit.stop.prevent="submit">
        <md-spinner md-indeterminate class="md-accent progress-custom" :class="{ 'show': loading}"></md-spinner>
        <md-input-container :class="{'md-input-invalid': errors.has('email')}">
          <label>Email</label>
          <md-input id="txt_email" v-model="email" v-validate="'required|email'" name="email" data-vv-name="email" :readonly="loading"></md-input>
          <span class="md-error">{{ errors.first('email') }}</span>
        </md-input-container>
        <md-input-container :class="{'md-input-invalid': errors.has('message')}">
          <label>Message</label>
          <md-textarea v-model="message" v-validate="'required|min:5|max:500'" maxlength="500" name="message" data-vv-name="message" :readonly="loading"></md-textarea>
          <span class="md-error">{{ errors.first('message') }}</span>
        </md-input-container>
        <br/>
        <md-button type="submit" class="md-raised md-accent btn-custom" :disabled="loading">
          <span v-if="!loading">Send</span>
          <span v-else>Sending</span>
        </md-button>
      </form>
    </md-whiteframe>
    <md-snackbar ref="validation_snackbar" md-duration="5000">
      <md-icon>report_problem</md-icon> &nbsp;
      <span>Please provide required fields.</span>
      <md-button class="md-accent" @click.native="$refs.validation_snackbar.close()">Retry</md-button>
    </md-snackbar>
    <md-snackbar ref="error_snackbar" md-duration="5000">
      <md-icon>report_problem</md-icon> &nbsp;
      <span>Error!</span>
      <md-button class="md-accent" @click.native="submit">Retry</md-button>
    </md-snackbar>
    <md-snackbar ref="success_snackbar" md-duration="10000">
      <md-icon>done</md-icon> &nbsp;
      <span>Message sent successfully!</span>
    </md-snackbar>
  </md-layout>
</template>
<script>
  import axios from 'axios'
  import { mapGetters } from 'vuex'

  export default {
    data () {
      return {
        email: '',
        message: '',
        loading: false
      }
    },
    computed: {
      ...mapGetters(['myInfo', 'sections'])
    },
    methods: {
      submit () {
        this.$validator.validateAll().then(() => {
          this.loading = true
          axios.post(this.myInfo.formspreeURL, {
            email: this.email,
            message: this.message
          }).then(() => {
            this.$refs['success_snackbar'].open()
          }).catch(() => {
            this.$refs['error_snackbar'].open()
          }).then(() => {
            this.loading = false
          })
        }).catch(() => {
          this.$refs['validation_snackbar'].open()
        })
      }
    }
  }
</script>
<style>
  .btn-custom {
    margin-left: 0!important;
  }
  
  .progress-custom {
    float: right;
    visibility: hidden;
  }
  
  .progress-custom.show {
    visibility: visible;
  }
  
  .contact-form {
    background-color: #3f51b5;
    background-image: url('~assets/img/backgrounds/pattern-large.png')!important;
    background-repeat: no-repeat;
    background-position-y: 100%;
    background-size: cover;
  }
  
  .contact-form form {
    margin-top: 3px;
    padding: 25px;
    background-color: #fff;
    background-color: rgba(255, 255, 255, 0.8);
  }
</style>



